import React from 'react';

function Details({ content, images, videos }) {
  const remainingImages = images.slice(1);

  return (
    <div className="details">
      <div className="content">
        {content}
      </div>

      <div className="images">
        {remainingImages.map((imageUrl) => (
          <img src={imageUrl} alt="" key={imageUrl} loading="lazy" />
        ))}
      </div>

      <div className="videos">
        {videos.map((video, videoIndex) => (
          <iframe
            key={videoIndex}
            title={`Video ${videoIndex + 1}`}
            autoPlay={true}
            src={video}
            style={{
              position: 'relative',
              top: 0,
              left: 0,
              width: '100%',
              height: '100%',
              aspectRatio: '16 / 9',
            }}
            loading="lazy"
          ></iframe>
        ))}
      </div>
    </div>
  );
}

export default Details;
