// Caption.js
import React from 'react';

function Caption({ title, tags, technologies, github }) {
    return (
        <div className="caption">
            <h3>{title}</h3>

            {Array.isArray(tags) &&
                tags.map((skill, idx) => (
                    <div key={idx} className="skill tag">
                        {skill}
                    </div>
                ))}

            {Array.isArray(technologies) &&
                technologies.map((skill, idx) => (
                    <div key={idx} className="skill technology">
                        {skill}
                    </div>
                ))}
            {github && github.trim() !== '' && (
                <div className="skill github">
                    <a href={github} target="_blank" rel="noopener noreferrer">
                        GitHub
                    </a>
                </div>
            )}
        </div>
    );
}

export default Caption;